import React, { Component } from 'react';
import './stylesheets/Header.css'

class Header extends Component {
	render() {
		window.$(document).ready(function(){
		  window.$("a").on('click', function(event) {
		    if (this.hash !== "") {
		      event.preventDefault();
		      var hash = this.hash;

		      window.$('html, body').animate({
		        scrollTop: window.$(hash).offset().top
		      }, 800, function(){
		        window.location.hash = hash;
		      });
		    } 
		  });
		});

		return (
			<div className="header">				
				<a data-scroll="" className="link" href="#about">About</a>
				<a data-scroll="" className="link" href="#projects">Projects</a>	
				<a data-scroll="" className="link" href="http://attachments.angel.co/2481770-3b8431170cd901dd0caacefd3339b4b5.pdf?1540667537" target="_blank">Resume</a>			
			</div>
		);
	}
}

export default Header;