import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Cover from './components/Cover';
import About from './components/About';
import Projects from './components/Projects';
import Blog from './components/Blog';
import { Route, withRouter } from 'react-router-dom';

class App extends Component {
  state = {
    blog: false
  }

  showBlog = () => {
    this.setState(prevState => ({
      blog: !prevState.blog
    }));
  }

  render() {
    let show = (
      <div>
          <p className="blog1" style={{fontSize: 20, marginLeft: 10, marginTop: 20}} onClick={this.showBlog}>Blog</p>
          <Cover />
          <About />
          <Projects />
          <footer className="footer">
            <p>Posted by: Gopesh Singhal</p>
            <p>Contact information: <a style={{textDecoration: 'none'}} href="mailto:gopeshsinghal301@gmail.com">gopeshsinghal301@gmail.com</a></p>
          </footer>
      </div>
    );

    if (this.state.blog) {
      show = (
        <div>
          <Blog />
        </div>
      );
    }

    return (
      <div className="App">        
        {show}
      </div>
    );
  }
}

export default withRouter(App);
